export default {
    name: "orange-handpick",
    props: [
        "total",
        "to",
        "content",
        "images",
        "copy_content",
        "showTime",
    ],
    methods: {
        openGoods() {
            let relation_id = this.$queue.getData("relation_id");
            if (relation_id) {
                uni.navigateTo({
                    url: "/pages/detail/goodsinfo?itemid=" + this.to + "&relation_id=" + relation_id,
                })
            } else {
                uni.navigateTo({
                    url: "/pages/detail/goodsinfo?itemid=" + this.to,
                })
            }
        },
        /**
         * 复制链接
         */
        copyHref() {
            let relation_id = this.$queue.getData("relation_id");
            if (relation_id) {
                uni.setClipboardData({
                    data: this.copy_content + "\n领券链接\n"+this.$queue.publicYuMing()+"/pages/detail/goodsinfo?itemid=" + this.to + "&relation_id=" + relation_id,
                    success: (r => {
                        this.$queue.showToast("素材复制成功，长按下方图片保存图片素材")
                    }),
                })
            } else {
                uni.setClipboardData({
                    data: this.copy_content + "\n领券链接\n"+this.$queue.publicYuMing()+"/pages/detail/goodsinfo?itemid=" + this.to,
                    success: (r => {
                        this.$queue.showToast("素材复制成功，长按下方图片保存图片素材")
                    }),
                })
            }
        },
        /**
         * 保存图片
         * @param url
         */
        toSave(url) {

            uni.getImageInfo({
                src: url,
                success: function (image) {
                    console.log('图片信息：', JSON.stringify(image));
                    uni.saveImageToPhotosAlbum({
                        filePath: image.path,
                        success: function () {
                            console.log('save success');
                            uni.showToast({
                                title: '图片保存成功',
                                icon: 'none',
                                duration: 3000
                            });
                        }
                    });
                }
            });


        },
    },
    filters: {
        formatDate: function (value) {
            value = parseInt(value + "000");
            let date = new Date(value);
            let y = date.getFullYear();
            let MM = date.getMonth() + 1;
            MM = MM < 10 ? ('0' + MM) : MM;
            let d = date.getDate();
            d = d < 10 ? ('0' + d) : d;
            let h = date.getHours();
            h = h < 10 ? ('0' + h) : h;
            let m = date.getMinutes();
            m = m < 10 ? ('0' + m) : m;
            let s = date.getSeconds();
            s = s < 10 ? ('0' + s) : s;
            return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s;
        }
    }
}
